*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.header-ul{
    display: flex;
}

.header-li{
    list-style: none;
    width: 60px;
    border: 1px solid skyblue;
    text-align: center;
}

@media ( min-width : 992px ) {
    .navlogin-hamburger-icon{
        display: none;
    }
}

.section-1{
    margin-top: 50px;
    margin-bottom: 50px;
}

.section-2, .section-3, .section-4{
    margin-bottom: 50px;
}

.section-2-1st-sub-post:hover, .section-2-2nd-sub-post:hover, .section-2-3rd-sub-post:hover {
    background-color: #fff;
    border: 1px dashed lightgray;
}

.section-1-for-img , #section-3-1st-post-for-img, #section-3-2nd-post-for-img, #section-3-3rd-post-for-img, #section-3-4th-post-for-img, #section-4-1st-post-for-img, #section-4-2nd-post-for-img, #section-4-3rd-post-for-img, #section-4-4th-post-for-img, #section-4-5th-post-for-img, #section-4-6th-post-for-img{
    overflow: hidden !important;
}

#section-1-img, #section-3-1st-post-img, #section-3-2nd-post-img, #section-3-3rd-post-img, #section-3-4th-post-img, #section-4-1st-post-img, #section-4-2nd-post-img, #section-4-3rd-post-img, #section-4-4th-post-img, #section-4-5th-post-img, #section-4-6th-post-img{
    transition: 0.4s all !important;
}

#section-1-img:hover{
    transform: scale(1.03) !important;
}

#section-3-1st-post-for-img:hover img, #section-3-2nd-post-for-img:hover img, #section-3-3rd-post-for-img:hover img, #section-3-4th-post-for-img:hover img, #section-4-1st-post-for-img:hover img, #section-4-2nd-post-for-img:hover img, #section-4-3rd-post-for-img:hover img, #section-4-4th-post-for-img:hover img,
#section-4-5th-post-for-img:hover img, #section-4-6th-post-for-img:hover img{
    transform: scale(1.03) !important;
}